<template>
<MyDialog title="Открыть услугу">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item v-bind="attrs" v-on="on">
      <v-list-item-title>Услугу</v-list-item-title>
    </v-list-item>
  </template>
  <v-card-text>
    <v-text-field autofocus @keyup.enter.prevent="open" v-model="id" label="ID"/>
  </v-card-text>
  <v-card-actions>
    <v-btn @click="open" :loading="loading" color="primary">Перейти</v-btn>
  </v-card-actions>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  data: () => ({
    id: '',
    loading: false
  }),

  methods: {
    async open () {
      if (this.id.match(/(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)/)) {
        this.loading = true;
        const { data } = await this.api.findFiltered('vds', { ip: this.id, status: '1,2,3,5' });
        this.loading = false;

        if (!data[0]) {
          alert(`Активный сервер с IP ${this.id} не найден.`);
          return;
        }

        this.tabMgr.open('Service', { elId: data[0].id });
        return;
      }

      this.tabMgr.open('Service', { elId: this.id });
    }
  }
}
</script>
